import { useState, useEffect, useMemo } from 'react';
import { Link, useNavigate } from 'react-router-dom'
import { Toast} from '../../utility/helper.js'

import AuthInput from '../../components/Auth/AuthInput/AuthInput.jsx'
import AuthPageContainer from '../../components/Auth/AuthPageContainer/AuthPageContainer.jsx'
import Button from '../../components/Button/Button.jsx'

import { useAuth } from '../../context/AuthContext.jsx'
import styles from './AdminLoginPage.module.scss'

export default function AdminLoginPage () {
  const [account, setAccount] = useState("");
  const [password, setPassword] = useState("");
  const { login, isAuthenticated } = useAuth();
  const navigate = useNavigate();

  const isValid = useMemo(() => {
        if (!account || account.length > 50) {
            return false;
        }
        if (!password || password.length > 50) {
            return false;
        }
        return true;
    }, [account, password]);

  const handleClick = async () => {

    if (!isValid) {
        Toast.fire({
            title: '!',
            icon: 'error',
        });
        return;
    }
    
    const success = await login({
      account,
      password,
      role:"admin"
    });
    // login success
    if (success) {
      Toast.fire({
        title: "",
        icon: "success",
      });
      return;
    // login fail
    } 
    Toast.fire({
        title: "",
        icon: "error",
      });
    await setPassword("")
  }

  useEffect(() => {
      if (isAuthenticated) {
        navigate('/admin/tweets');
      }
    }, [navigate, isAuthenticated]);

  return (
    <AuthPageContainer title=''>
      <AuthInput label='' value={account} placeholder='' onChange={(accountInputValue) => setAccount(accountInputValue)}
      notification='!' wordsLimit={50}
      />
      <AuthInput label='' type='password' value={password} placeholder='' onChange={(passwordInputValue) => setPassword(passwordInputValue)}
      notification='!' wordsLimit={50}
      />
      <Button title='' size='large' isAction onClick={handleClick}></Button>
      <div className={styles.link}>
        <Link to="/login">
          <span className={styles.span}></span>
        </Link>
      </div>
    </AuthPageContainer>
  );
}